import { Component, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Category } from 'src/app/models/category.model';
import { NgbModalRef, NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'app-category-details',
  templateUrl: './category-details.component.html',
  styleUrls: ['./category-details.component.scss']
})
export class CategoryDetailsComponent implements OnInit, AfterViewInit {
  message: string;
  category: Category;
  attributes: string[];

  @ViewChild('content') content: ElementRef<HTMLElement>;
  private modal: NgbModalRef;

  constructor(private modalService: NgbModal, private router: Router) {
  }

  ngOnInit(): void {
    this.category = history.state.resource;

    if (!this.category)
    {
      this.ok();
    }
    this.attributes = Object.keys(this.category);
    // tslint:disable-next-line: no-string-literal
    this.message = 'Details of category: ' + this.category['name'];
  }

  ngAfterViewInit(): void {
    const options: NgbModalOptions = {
      size: 'md',
      backdrop: 'static',
    };
    this.modal = this.modalService.open(this.content, options);
  }

  ok(): void {
    this.router.navigateByUrl('/categories');
    if (this.modal)
    {
      this.modal.dismiss();
    }
  }
}
