import { Component, OnInit } from '@angular/core';
import { Category } from 'src/app/models/category.model';
import { ResourceDeleteContext } from 'src/app/shared/dashboard-resource/resource-delete/resource-delete.component';

@Component({
  selector: 'app-category-delete',
  templateUrl: './category-delete.component.html',
  styleUrls: ['./category-delete.component.scss']
})
export class CategoryDeleteComponent implements OnInit {
  context: ResourceDeleteContext<Category>;

  constructor() { }

  ngOnInit(): void {
    this.context = {
      deleteUrl: 'categoriesDeleteUrl',
      data: history.state.resource,
      title: 'Delete Category Resource!',
      description: 'Are you sure to delete this category?'
    };
  }

}
