import { Component, OnInit, ViewChild } from '@angular/core';
import { Category } from 'src/app/models/category.model';
import { ITableContext, TableViewComponent } from 'src/app/shared/dashboard-resource/table-view/table-view.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent {
  data: Category[];
  context: ITableContext<Category> = {
    urls: {
      createUrl: '/categories/create',
      detailsUrl: '/:id'
    },
    data: this.data,
    properties: [
      'name', 'description', 'created_at', 'updated_at'
    ],
    title: 'Categories'
  };
  @ViewChild('table') table: TableViewComponent<Category>;

  constructor(route: ActivatedRoute) {
    // console.log(this.context);
    route.data.subscribe(data => {
      this.data = data.categories;
      this.context.data = this.data;
    });
  }
}
